@use "../../styles/variables" as *;

.card {
    display: flex;
    border-top: 0.1rem dashed $color-dark-grey;
    width: 80%;
    padding: 2rem 0;
    font-family: $font-primary;
    color: $color-black;
    font-size: $font-size-medium;

    .card__image {
        height: 18rem;
        width: auto;
        opacity: 0.8;
        transition: 500ms;
    }

    .card__container {
        padding: 1rem;

        .card__header {
            font-size: $font-size-large;
            margin: 0.5rem 0;
        }

        .card__list {
            display: flex;
            margin: 0.5rem 0;
            flex-wrap: wrap;

            .card__item {
                margin-left: 2.5rem;
            }
        }
    }
}

.card-reverse {
    flex-direction: row-reverse;
    justify-content: flex-end;
}

.card__image:hover {
    opacity: 1;
}

@media screen and (min-width: 1450px) {
    .card {
        width: 70%;
    }
}

@media screen and (max-width: 1300px) {
    .card__text {
        font-size: $font-size-small;
    }
}

@media screen and (max-width: 1100px) {
    .card__item {
        font-size: $font-size-small;
    }

    .card__image {
        height: 13rem !important;
    }
}

@media screen and (max-width: 890px) {
    .card {
        flex-direction: column;
        align-items: center;
    }
}

@media screen and (max-width: 450px) {
    .card__image {
        display: none;
    }
}